<template>
  <ion-page>
    <ion-header :translucent="true">
      <ion-toolbar>
        <ion-buttons slot="start">
          <ion-menu-button color="primary"></ion-menu-button>
        </ion-buttons>
        <ion-title>Surat Keluar</ion-title>
      </ion-toolbar>
    </ion-header>
    
    <ion-content :fullscreen="true">
    
          <DxDataGrid
            :data-source="dataSource"
            :show-borders="true"
            key-expr="id_surat_keluar"
            :column-hiding-enabled="true"
            >
            <DxPaging :enabled="false"/>
            <DxEditing
                :allow-updating="true"
                :allow-adding="true"
                :allow-deleting="true"
                mode="popup"
            >
                <DxPopup
                :show-title="true"
                :width="700"
                :height="525"
                title="Surat Keluar"
                />
            </DxEditing>

            <DxColumn data-field="no_surat"/>
            <DxColumn data-field="perihal_surat"/>
            <DxColumn data-field="tujuan"/>
            <DxColumn data-field="tgl_dibuat"/>
            <DxColumn data-field="agenda"/>
            <DxColumn data-field="sifat_surat"/>
            <DxColumn 
            data-field="status"
            :encode-html="false"
            :form-item="{visible:false}"
            :customize-text="statusColumn"
            />
            <DxColumn 
            :form-item="{visible:false}"
            data-field="file_surat_keluar"/>
            
            <DxScrolling column-rendering-mode="virtual"/>
      </DxDataGrid>
      <!-- </div> -->
    </ion-content>
  </ion-page>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

import { IonButtons, IonContent, IonHeader, IonMenuButton, IonPage, IonTitle, IonToolbar } from '@ionic/vue';
import {
  DxDataGrid,
  DxColumn,
  DxPaging,
  DxEditing,
  DxPopup,
  DxLookup,
  DxForm,
  DxScrolling
} from 'devextreme-vue/data-grid';
import CustomStore from 'devextreme/data/custom_store';
import { DxItem } from 'devextreme-vue/form';
import 'devextreme/dist/css/dx.material.teal.light.compact.css';
import axios from 'axios';
import Swal from 'sweetalert2';

const Toast = Swal.mixin({
  toast: true,
  position: 'top-end',
  showConfirmButton: false,
  timer: 3000,
  timerProgressBar: true,
  didOpen: (toast) => {
    toast.addEventListener('mouseenter', Swal.stopTimer)
    toast.addEventListener('mouseleave', Swal.resumeTimer)
  }
})
// const API_URL = 'http://127.0.0.1:8000/api/';
const API_URL = 'https://esurat-um.com/api/';

const store = new CustomStore({
	key: 'id_surat_keluar',
	load: function() {
        return axios.get(API_URL + 'surat-keluar').then(response => response.data);
	},
	insert: (values) => {
		return axios.post(API_URL +'surat-keluar',values).then(function(response){  
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		}).catch((response) => {
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		})
	},
	remove: (key) => {
		// return CrudService.removeData('surat-keluar',key);
        return axios.delete(API_URL +'surat-keluar'+"/"+key).then(function(response) {
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		}).catch((response) => {
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		})
	},
	update: (key, values) => {
		// return CrudService.updateData('surat-keluar',key,values);
        return axios.put(API_URL +'surat-keluar'+"/"+key,values).then(function(response){
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		})
		.catch((response)=>{
			Toast.fire({
				icon: response.data.status,
				title: response.data.message
			})
		})
	}
});

export default defineComponent ({
  name: 'SuratKeluar',
  components: {
    IonButtons,
    IonContent,
    IonHeader,
    IonMenuButton,
    IonPage,
    IonTitle,
    IonToolbar,
    DxDataGrid,
    DxColumn,
    DxPaging,
    DxEditing,
    DxPopup,
    DxScrolling
  },
  data() {
    return {
      dataSource: store,
    };
  },
  methods: {
    // statusColumn(e) {
    //     const stext = ["Draft","Waiting Approval","Approved","Rejected",""];

    //     return stext[e.value];
    // }
  },
})  
</script>

